import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import { 
	Panel, 
	PanelHeader, 
	Placeholder,
	Header, 
	Button, 
	Group, 
	Cell, 
	Div, 
	Avatar,
	Tabbar,
	TabbarItem,
} from '@vkontakte/vkui';

import { 
	Icon28MarketAddBadgeOutline,
	Icon24ErrorCircleOutline,
	Icon28Profile,
	Icon28MarketOutline,
	Icon28FavoriteOutline,
	Icon24ClockOutline,
} from '@vkontakte/icons';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>

					<Panel id='home' fetchedUser={fetchedUser} go={go}> 
						<PanelHeader>Главная</PanelHeader>
						

						{fetchedUser &&
						<Group>
							<Cell
								disabled
								before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
								description={fetchedUser.id}
							>
								Здравствуйте, {`${fetchedUser.first_name} ${fetchedUser.last_name}`}!
							</Cell>
						</Group>}

						{!fetchedUser && 
						<Placeholder
							icon={<Icon24ErrorCircleOutline width={56} height={56} />}
							header="Не удалось получить данные"
							action={<Button size="m">Загрузить профиль</Button>}
							>
								Нажмите ниже на кнопку, чтобы загрузить профиль.
						</Placeholder>}
					</Panel>

					<Panel id='pay' go={go}>
						<PanelHeader>Покупка</PanelHeader>

						<Placeholder
							icon={<Icon24ClockOutline width={56} height={56} />}
							header="Упс!"
							>
								Этот раздел временно не доступен.
						</Placeholder>
					</Panel>

					<Panel id='rating' go={go}>
						<PanelHeader>Рейтинг</PanelHeader>

						<Placeholder
							icon={<Icon24ClockOutline width={56} height={56} />}
							header="Упс!"
							>
								Этот раздел временно не доступен.
						</Placeholder>
					</Panel>

				</View>

				<Tabbar>
					<TabbarItem selected={activePanel === 'home'} onClick={go} data-to="home" text="Мой профиль">
						<Icon28Profile />
					</TabbarItem>

					<TabbarItem selected={activePanel === 'pay'} onClick={go} data-to="pay" text="Покупка">
						<Icon28MarketOutline />
					</TabbarItem>

					<TabbarItem selected={activePanel === 'rating'} onClick={go} data-to="rating" text="Рейтинг">
						<Icon28FavoriteOutline />
					</TabbarItem>

				</Tabbar>

			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
